<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
                <label class="vs-input--label">{{ $t('ACCOUNT_NUMBER') }}</label>
                <account-lookup :title="$t('ACCOUNT_NUMBER')"
                                popupRef="accountNumberPopup"
                                typeBehaviourEnumId="ACCT_TYPE_CURRENT"
                                valueField="accountNumber"
                                displayField="accountNumber"
                                @select-record="selectAccountRecord"/>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('DELIVERY_BRANCH') }}</label>
          <branch-lookup :title="$t('DELIVERY_BRANCH')" :placeholderOnly="true" disabled="N" valueField="branchId" displayField="branchName" @select-record="selectBranchRecord"/>
        </div>
      </div>
      <div class="nv-row mb-4">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('BOOK_COUNT') }}</label>
          <div class="flex">
            <vs-input-number min="1" v-model="bookCount" />
          </div>
        </div>
      </div>
      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <div class="dropdown-button-container">
              <vs-button class="btnx" type="filled" @click="createChequeBook('browse')">{{ $t('SUBMIT') }}</vs-button>
              <vs-dropdown vs-trigger-click>
                <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
                <vs-dropdown-menu>
                  <vs-dropdown-item @click="createChequeBook('stay')">
                    <span>{{ $t('SUBMIT_STAY') }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import ChequeService from '@/services/cheque/ChequeService'
import AccountLookup from '@/views/account/account-root/AccountLookup'
import BranchLookup from '@/views/bank/branch/BranchLookup'

export default {
  name: 'cheque-book-create',
  mixins: [AlertMixin],
  components: {
    AccountLookup,
    BranchLookup
  },
  data: function () {
    return {
      accountNumber: '',
      bookCount: 1,
      deliveryBranchId: '',
      deliveryBranchName: ''
    }
  },
  methods: {
    selectAccountRecord (record) {
      this.accountNumber = record.accountNumber
    },
    selectBranchRecord (record) {
      this.deliveryBranchId = record.branchId
      this.deliveryBranchName = record.branchName
    },
    createChequeBook (nextAction) {
      let self = this
      ChequeService.createChequeBook({
        accountNumber: this.accountNumber,
        deliveryBranchId: this.deliveryBranchId,
        bookCount: this.bookCount
      }).then(function (response) {
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
          } else {
            self.$router.push({ name: 'BrowseChequeBookWorkflowInstance' })
          }
        }
      }).catch(function (error) {
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  }
}
</script>
