<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="branchTableData" :noDataText="branchTableNoDataText" :sst="true" search @search="filterBranchTable" @sort="orderBranchTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadBranchTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="branchTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ branchTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="branchTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="branchId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="branchName">
            {{ this.$t('BRANCH_NAME') }}
          </vs-th>
          <vs-th sort-key="bankName">
            {{ this.$t('BANK_NAME') }}
          </vs-th>
          <vs-th sort-key="branchCode">
            {{ this.$t('BRANCH_CODE') }}
          </vs-th>
          <vs-th sort-key="swiftCode">
            {{ this.$t('SWIFT_CODE') }}
          </vs-th>
          <vs-th sort-key="countryName">
            {{ this.$t('COUNTRY') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].branchId">
              {{ data[index].branchId }}
            </vs-td>
            <vs-td :data="data[index].branchName">
              <span :class="disabledClass(data[index])">{{ data[index].branchName | placeholder('-') }}</span>
            </vs-td>
            <vs-td :data="data[index].bankName">
              <span :class="disabledClass(data[index])">{{ data[index].bankName }}</span> <i v-if="data[index].licensee === 'Y'" class="far fa-award ml-2 text-primary"></i>
            </vs-td>
            <vs-td :data="data[index].branchCode">
              {{ data[index].branchCode | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].swiftCode">
              {{ data[index].swiftCode | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].countryName">
              {{ data[index].countryName }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>
      <vs-pagination :total="branchTableTotalPages" v-model="branchTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import BranchTableMixin from '@/mixins/bank/BranchTableMixin'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'

export default {
  name: 'branch-popup',
  mixins: [PopupMixin, BranchTableMixin, NumberStyleMixin],
  mounted: function () {
    this.loadBranchTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
